	module.exports = {"data":{"wordpressPost":{"title":"This $11 Concealer Is A Beauty Influencer Fave And You Need One","content":"<p>Here&#8217;s a handy little fact that may or may not come as a surprise. Roughly 25 per cent of of Australian women spend more than <strong>$150 on beauty buys every SINGLE month. </strong>There&#8217;s no doubt in our minds that concealer is included in that swag.</p>\n<p>And, the same research shows, we’re prepared to go to great lengths to ensure we have the cash to splash. One in five of us will happily ditch dinner plans with friends, and one in 10 will even overdraw the bank account or skip a phone bill. Um ladies! We applaud your commitment, but let&#8217;s get sensible here. You need to eat.</p>\n<p>Given that you&#8217;ll be straight-up delighted to discover the <strong>LA Girl Pro Concealer</strong> if you haven&#8217;t already. This snappily-priced tube of sheer genius is used and loved by beauty influencers the world over. Not surprisingly, it&#8217;s also gaining a following amongst us everyday folk &#8211; in fact, it&#8217;s currently the number one selling make-up item for 2017 at online beauty retailer<a href=\"http://www.ry.com.au\"> RY.com.au</a>.</p>\n<p><strong>Jade Dowling, senior beauty therapist</strong> at <a href=\"http://www.ry.com.au\">RY.com.au</a> tells us why and also, provides a few handy tips on how to make it work for you.</p>\n<figure id=\"attachment_2778\" style=\"width: 169px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2778\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30133231/Jade-Dowling-169x300.jpg\" alt=\"\" width=\"169\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30133231/Jade-Dowling-169x300.jpg 169w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30133231/Jade-Dowling.jpg 548w\" sizes=\"(max-width: 169px) 100vw, 169px\" /><figcaption class=\"wp-caption-text\">Say hello to Jade Dowling. She knows stuff.</figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>What makes this product so good?</strong></p>\n<p>Apart from the fact that it’s very cheap and seriously works as well as more expensive concealers, I think a big reason this is popular is because of the range of shades. There are 25 of them, so it suits all skin types and is really versatile in that you can use it to conceal, correct, contour or simply brighten.</p>\n<p>Colour correctors tend to be products where you can generally get away with a cheaper price. They’re worn under your top layers of make-up, so if blended and set correctly, they have a greater likelihood of living up to their claims of being long-lasting – compared to foundations and powders.</p>\n<p>But be careful to watch for creasing and don’t overdo it! <strong>LA Girl Concealers</strong> tend to have more pigmentation, which is great for the life of the product (as you need less per use) but if you use too much of one colour, you can look like a bit like abstract art.</p>\n<figure id=\"attachment_2779\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2779\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30133319/LAGIRL1-300x171.jpg\" alt=\"\" width=\"300\" height=\"171\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30133319/LAGIRL1-300x171.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30133319/LAGIRL1-768x437.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30133319/LAGIRL1.jpg 800w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Yes. It IS magical! Well, terribly good anyway.</figcaption></figure>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p><strong>How do you pick the right shade?</strong></p>\n<p>If you’re just dabbling in correctors, the more outrageous shades can be frightening but I assure you, it’s far easier than it seems.</p>\n<p>Firstly, take a look in the mirror and work out what you want to cover up. The general rule of thumb is to pick the opposite colour, and if you’re really not sure just Google ‘corrector colour wheel’ images and use that as a guide.</p>\n<p>Typically, people want to either <strong>cover dark circles under their eyes or red marks and blemishes</strong>. Or both, and you can use multiple colours.</p>\n<p>For <strong>dark circles</strong>, go for an orange or depending on how dark your skin tone is a red concealer. This cancels out the blue and purple undertones of the circles.</p>\n<figure id=\"attachment_2784\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2784\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30134130/colour-wheel-JPG-300x287.jpg\" alt=\"\" width=\"300\" height=\"287\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30134130/colour-wheel-JPG-300x287.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30134130/colour-wheel-JPG.jpg 453w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">You&#8217;ll find handy info like this. Image: Pinterest.</figcaption></figure>\n<p>&nbsp;</p>\n<p>In those places where <strong>you have redness</strong> come through, usually around the nose, or blemishes, go for the opposite of red – which is green.</p>\n<p>The more adventurous of you will also have a lavender shade, which is great for neutralising any <strong>unwanted yellow tinges</strong> to the skin or simply as a highlighter to counteract the sallowness. I also like to finish my look with a purple-based powder, to help brighten the face all over.</p>\n<p>If that’s too advanced, just go for one that looks natural, or with yellow undertones, that is one or two shades lighter than your skin tones. These are more universal and you can use it to cover up <strong>under-eye circles, age spots, freckles and pesky pimples</strong>.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/07/30133130/LAGIRL2.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"c1770f62-b17f-53a1-9354-1ec67055307f"}}

